.backdrop__map {
  width: 100vw;
  height: 100vh;
  z-index: 10;
  opacity: 1;
  background-color: rgba(246, 158, 203, .4);
  transition: opacity .25s ease-in-out, visibility .25s ease-in-out;
  position: fixed;
  top: 50%;
  left: 50%;
  overflow-y: scroll;
  transform: translate(-50%, -50%);
}

