.third-title {
	font-family: titan one;
	@include font(400, 18px, 1.17em);
	text-transform: uppercase;
	color: var(--preTitleColor);

	@media screen and(min-width:768px) {
		font-size: 26px;
		line-height: 1.15em;
	}

	@media screen and(min-width:1080px) {
		font-size: 30px;
		line-height: 1.13em;
		letter-spacing: 0.04em;
	}
}
